import React, { useEffect, useState } from "react";
import Login from './components/Login';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

import { NavBar } from "./components/NavBar";
import { Banner } from "./components/Banner";
import { Skills } from "./components/Skills";
import { Projects } from "./components/Projects";
import { Contact } from "./components/Contact";
import { Footer } from "./components/Footer";

import { auth } from './firebase';
import { onAuthStateChanged } from 'firebase/auth';

function App() {
  const [usuario, setUsuario] = useState(null);
  const [vozActiva, setVozActiva] = useState(true); // control de voz

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      setUsuario(user);
    });
    return () => unsubscribe();
  }, []);

  useEffect(() => {
    const handleMouseOver = (e) => {
      if (!vozActiva) return;
      if (e.target.hasAttribute("data-no-voz")) return;

      const texto = e.target.getAttribute("data-voz") || e.target.innerText;
      if (texto && texto.trim().length > 0) {
        const voz = new SpeechSynthesisUtterance(texto.trim());
        voz.lang = "es-MX";
        speechSynthesis.cancel();
        speechSynthesis.speak(voz);
      }
    };

    document.addEventListener("mouseover", handleMouseOver);
    return () => {
      document.removeEventListener("mouseover", handleMouseOver);
    };
  }, [vozActiva]);

  return (
    <div className="App">
      {usuario ? (
        <>
          {/* PASAMOS las props al NavBar */}
          <NavBar vozActiva={vozActiva} setVozActiva={setVozActiva} />
          <Banner />
          <Skills />
          <Projects />
          <Contact />
          <Footer />
        </>
      ) : (
        <Login />
      )}
    </div>
  );
}

export default App;
